import React from 'react';
import PropTypes from 'prop-types';
import './Movie.css';

function Movie({id, title, summary, poster, rating, runtime, year, genres}) {
  return (
    <div className="movie-container">
      <img src={poster} alt={title}></img>
      <div className="movie_title">
        <span className="title_text">{title}</span>
      </div>
      <h4 className="movie_year">{year}</h4>
      <div className="movie_summary">{summary}</div>
      <div className="movie_info">
        <div className="info info_runtime">Running Time: {runtime}</div>
        <div className="info info_rating">Rating Score: {rating}</div>
        <div className="info info_genres">Genres: {genres.join(' | ')}</div>
      </div>
      <hr className="divide-line"></hr>
    </div>
  );
}

Movie.propTypes = {
  id: PropTypes.number.isRequired,
  title: PropTypes.string.isRequired,
  summary: PropTypes.string.isRequired,
  poster: PropTypes.string.isRequired,
  rating: PropTypes.number.isRequired,
  runtime: PropTypes.number.isRequired,
  year: PropTypes.number.isRequired,
  genres: PropTypes.array,
};

export default Movie;
